// Vue
import Vue from 'vue'

var lazyload = {
  // Vue.use() install,Vue
  install(vue, payload) {
    // 
    if (!Array.prototype.remove) {
      Array.prototype.remove = function (item) {
        if (!this.length) return
        var index = this.indexOf(item);
        if (index > -1) {
          this.splice(index, 1);
          return this
        }
      }
    }

    // 
    var defaultImage = payload.defaultImage || require('../assets/zoom-lazy-logo.gif');
    var errorImage = payload.errorImage || require('../assets/zoom-picture-not-found.jpg');

    // 10px
    var distanece = payload.scrollDistance || 10;
    // 
    var listenList = [];
    // 
    var imageCacheList = [];

    // 
    const isAlredyLoad = (imageSrc) => {
      if (imageCacheList.indexOf(imageSrc) > -1) {
        return true;
      } else {
        return false;
      }
    }

    //
    const isCanShow = (item) => {
      var ele = item.ele;
      var src = item.src;
      //
      var top = ele.getBoundingClientRect().top;
      //
      // var windowHeight = window.innerHight;
      // top - distance distance
      if (top - distanece < window.innerHeight) {
        var image = new Image();
        image.src = src;
        image.onload = function () {
          ele.src = src;
          imageCacheList.push(src);
          listenList.remove(item);
        }
        image.onerror = function () {
          ele.src = errorImage;
          imageCacheList.push(src);
          listenList.remove(item);
        }
        return true;
      } else {
        return false;
      }
    };

    const onListenScroll = () => {
      window.addEventListener('scroll', function () {
        var length = listenList.length;
        for (let i = 0; i < length; i++) {
          isCanShow(listenList[i]);
        }
      })

    }

    //Vue 
    const addListener = (ele, binding) => {
      //
      var imageSrc = binding.value;

      // src
      if (isAlredyLoad(imageSrc)) {
        ele.src = imageSrc;
        return false;
      }

      var item = {
        ele: ele,
        src: imageSrc
      }

      //
      ele.src = defaultImage;

      //
      if (isCanShow(item)) {
        return
      }

      //lisenList
      listenList.push(item);

      //scroll
      onListenScroll();
    }

    Vue.directive('lazyload', {
      inserted: addListener,
      updated: addListener
    })
  }
}

export default lazyload;