<template>
  <div class="zoom-slider" ref="zoom-slider">
    <div class="slider-process" :style="{width}"></div>
    <div class="zoom-thunk" ref="zoom-thunk" :style="{left}">
      <div class="slider-block"></div>
      <div class="slider-tips">
        <span>{{ ( scale * 100 ).toFixed() }}%</span>
      </div>
    </div>
  </div>
</template>
<script>
/*
 * min 
 * max 
 * v-model 
 * */
export default {
  name: "zoom-slider",
  props: {
    color: String, //  , 
    min: {
      //  
      type: [Number, String],
      default: 0
    },
    max: {
      //  
      type: [Number, String],
      default: 100
    },
    value: {
      //   v-model
      type: [Number, String],
      default: 0
    }
  },
  data() {
    return {
      slider: null, //DOM
      thunk: null, //DOM
      per: this.value || 0 //
    };
  },
  //
  mounted() {
    if (this.per > this.max) {
      this.per = null;
      throw new Error("zoom-ui Error: ! ");
    }
    this.slider = this.$refs["zoom-slider"];
    this.thunk = this.$refs["zoom-thunk"];
    this.thunk.onmousedown = e => {
      let width = parseInt(this.width);
      let disX = e.clientX;
      document.onmousemove = e => {
        // value, left, width
        // valueleftwidth
        // width
        let newWidth = e.clientX - disX + width;
        // 
        let scale = newWidth / this.slider.offsetWidth;
        this.per = Math.ceil((this.max - this.min) * scale + this.min);
        this.per = Math.max(this.per, this.min);
        this.per = Math.min(this.per, this.max);
        // 
        this.$emit("input", this.per);
      };
      document.onmouseup = function() {
        document.onmousemove = document.onmouseup = null;
      };
      return false;
    };
  },
  computed: {
    // slidertrunkleft
    //   -/- = sliderwidth / sliderwidth
    // trunk left =  sliderwidth + trunk/2
    scale() {
      return (this.per - this.min) / (this.max - this.min);
    },
    width() {
      if (this.slider) {
        return this.slider.offsetWidth * this.scale + "px";
      } else {
        return 0 + "px";
      }
    },
    left() {
      if (this.slider) {
        return (
          this.slider.offsetWidth * this.scale -
          this.thunk.offsetWidth / 2 +
          "px"
        );
      } else {
        return 0 + "px";
      }
    }
  }
};
</script>
<style>
.zoom-slider {
  position: relative;
  margin: 20px 0;
  width: 400px;
  height: 10px;
  background: #e4e7ed;
  border-radius: 5px;
  cursor: pointer;
}
.zoom-slider .slider-process {
  position: absolute;
  left: 0;
  top: 0;
  width: 112px;
  height: 10px;
  border-radius: 5px;
  background: #409eff;
}
.zoom-slider .zoom-thunk {
  position: absolute;
  left: 100px;
  top: 0;
  transform: translateY(-25%);
  width: 20px;
  height: 20px;
}
.zoom-slider .slider-block {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid #409eff;
  background: rgba(255, 255, 255, 1);
  transition: 0.5s all;
}
.zoom-slider .slider-tips {
  position: absolute;
  left: -7px;
  bottom: 30px;
  min-width: 15px;
  text-align: center;
  padding: 4px 8px;
  background: #999;
  border-radius: 5px;
  height: 24px;
  line-height: 1;
  color: #fff;
}
.zoom-slider .slider-tips i {
  position: absolute;
  margin-left: -5px;
  left: 50%;
  bottom: -9px;
  font-size: 16px;
  color: #000;
}
.zoom-slider .slider-block:hover {
  transform: scale(1.1);
  opacity: 0.6;
}
</style>