<template>
  <!-- submit.prevent="false"  -->
  <form
    :class="[inline ? 'form-inline' : '', textAlign ? 'form-align-' + textAlign : '' ]"
    class="zoom-form"
  >
    <slot></slot>
  </form>
</template>
<script>
export default {
  name: "zoom-form",
  props: {
    textAlign: {
      type: String,
      default: null
    },
    inline: {
      type: [String, Boolean],
      default: null
    },
    labelWidth: {
      type: [String, Boolean],
      default: null
    }
  },
  methods: {
    /**
     *  form,handleBlur 
     */
    valid() {
      let arr = this.$children;
      let count = 0;
      arr.forEach(item => {
        let modules = item.$slots.default;
        if (modules && modules.length) {
          modules.forEach(i => {
            let componentInstance = i.componentInstance;
            /**
             * (require)
             * require, ,  ''
             * 2s 
             */
            if (
              item.require &&
              componentInstance &&
              componentInstance.handleBlur
            ) {
              let res = componentInstance.handleBlur();
              if (!res || !componentInstance.currentValue) {
                let msg = componentInstance.errMsg;
                componentInstance.errMsg = "";
                componentInstance.error = true;
                res = false;
                // TODO 2S 
                setTimeout(() => {
                  componentInstance.errMsg = msg;
                  componentInstance.error = false;
                }, 2000);
              }
              /**
               * , count + 1
               * //  res 0  true
               */
              if (!res) count += 1;
            }
          });
        }
      });
      return !count;
    },
    // 
    /**
     * 
     * form zoomreset, 
     */
    reset() {
      let arr = this.$children;
      arr.forEach(item => {
        //  zoom-form-item 
        let modules = item.$slots.default;
        if (modules && modules.length) {
          modules.forEach(i => {
            if (i.componentInstance && i.componentInstance.reset) {
              i.componentInstance.reset();
            }
          });
        }
      });
    }
  }
};
</script>
<style>
.zoom-form {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
</style>