<template>
  <a @click="logoutClick" class="zoom-logout">{{title}}</a>
</template>
<script>
export default {
  name: "zoom-logout",
  props: {
    op: {
      type: Object,
      url: String, //  url
      point: [Boolean, String], //  
      onClick: Function, //  
      text: String //  , 
    },
    url: String,
    text: String,
    point: [Boolean, String]
  },
  data() {
    return {
      confim: false, //  
      logoutUrl: null,
      title: ""
    };
  },
  created() {
    if (this.op) {
      if (this.op.point) {
        // 
        this.confim = this.op.point;
      }
      if (this.op.url) {
        this.logoutUrl = this.op.url;
      }
      if (this.op.text) {
        this.title = this.op.text;
      }
    } else {
      if (this.point) {
        this.confim = this.point;
      }
      if (this.url) {
        this.logoutUrl = this.url;
      }
      if (this.text) {
        this.title = this.text;
      }
    }
  },
  methods: {
    // 
    logoutClick() {
      if (this.op && this.op.onClick) {
        this.op.onClick();
      }
      let content = "";
      // 
      if (this.confim) {
        if (typeof this.confim === "string") {
          content = this.confim;
        } else {
          content = "?";
        }
        this.$zoom.popup({
          title: "",
          type: "query",
          content,
          onClick: () => {
            this.logout();
          }
        });
      } else {
        this.logout();
      }
    },
    logout() {
      this.$zoom.cookie.clear();
      sessionStorage.clear();
      window.location.reload(true);
      if (this.logoutUrl) {
        window.location.href = this.logoutUrl;
      }
    }
  }
};
</script>
<style>
.zoom-logout {
  cursor: pointer;
}
</style>